@use "../../../index.scss";

.add-button {
  font-family: index.$noto-sans;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  width: 137px;
  height: 55px;
  background: #3DB46D;
  color: white;
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}