@use "../../../index.scss";

.search {
   text-align: left;
   font-family: index.$noto-sans;
   display: flex;
   align-items: center;

  .logo {
    display: flex;
    flex-direction: column;
    font-size: 9px;
    font-weight: 500;
    line-height: 12px;
    letter-spacing: 0em;
    cursor: pointer;

    &:first-child {
      font-size: 14px;
      font-weight: 800;
      line-height: 19px;
      letter-spacing: 0em;
    }
  }

  .search-box {
    width: 300px;
    height: 55px;
    border: 1px solid index.$gray;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;

    .icon {
      color: index.$gray;
      width: 17px;
      height: 17px;
    }

    input {
      border: none;
      padding: 13px;
      height: 19px;
      width: 80%;
      outline: none;

      &::placeholder {
        font-family: index.$noto-sans;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 19px;
      }
    }
  }
}