@use "../../../index.scss";

.modal {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: index.$noto-sans;
  z-index: 100;

  .modal-input {
    width: 620px;
    height: 367px;
    background: #FFFFFF;
    border-radius: 12px;
    padding: 30px;

    .label-container {
      display: flex;
      flex-direction: column;
      margin-top: 10px;

      label {
        font-weight: 500;
        font-size: 14px;
        line-height: 19px;
        color: #4F4F4F;
        margin: 10px 0 10px 5px;
      }

      input {
        width: 552.33px;
        height: 55px;
        border: 1px solid #4F4F4F;
        filter: drop-shadow(0px 1px 6px rgba(0, 0, 0, 0.1));
        border-radius: 12px;
        padding: 0 10px;
        text-overflow: ellipsis;
      }

      .error {
        border: 1px solid red;
      }

      .error-label {
        font-size: 12px;
        margin-left: 5px;
        margin-top: 5px;
        color: red;
      }
    }

    .button {
      display: flex;
      justify-content: right;
      margin-top: 15px;
      align-items: center;

      .cancel-button {
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
        color: #BDBDBD;
        cursor: pointer;
      }

      .delete-button {
        padding: 12px 18px;
        background: red;
        box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
        border-radius: 12px;
        color: #FFFFFF;
        font-size: 16px;
        font-weight: 700;
        margin-left: 10px;
        cursor: pointer;
      }
    }
  }
}