@use "../../../index.scss";

.masonry-item {
  overflow: hidden;
  position: relative;
  border-radius: 16px;
  margin: 20px 0;

  .masonry-overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.38);
    top: 0;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
  }

  .masonry-text {
    position: absolute;
    width: 289.53px;
    height: 44px;
    bottom: 10px;
    left: 20px;
    font-family: index.$montserrat;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: rgba(255, 255, 255, 1);
    z-index: 3;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
  }

  .delete-button {
    position: absolute;
    top: 10px;
    right: 10px;
    font-family: index.$montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;
    text-transform: lowercase;
    color: #EB5757;
    border: 1px solid #EB5757;
    padding: 5px 15px;
    z-index: 3;
    cursor: pointer;
    border-radius: 38px;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
  }

  &:hover {
    .delete-button {
      opacity: 1;
    }
    .masonry-text {
      opacity: 1;
    }
    .masonry-overlay {
      opacity: 1;
    }
  }
}

.image {
  overflow: hidden;
}

.small {
  height: 306px;
  width: 383px;
}

.medium {
  height: 582.5px;
  width: 385px;
}